export default {
    state:{
        questionAnswer:[
            {
                question : 'Гражданином какой страны вы являетесь?',
                type: 'input',
                placeholder: 'Введите вашу страну'
            },
            {
                type: 'radio',
                question: 'В каком регионе Вы находитесь?',
                answers: [
                    'Краснодар',
                    'Краснодарский край',
                    'Другой регион'
                ]
            },
            {
                question : 'Укажите Ваш возраст',
                type: 'input',
                numeric: true,
                placeholder: 'Введите ваш возраст'
            },
            {
                question : 'Сколько лет проживаете на территории РФ?',
                type: 'radio',
                answers: [
                    'До 6 месяцев',
                    'От 6  до 12 месяцев',
                    '1-3 года',
                    '3-5 лет',
                    'Более 5 лет'
                ]
            },
            {
                question : 'Какой вид документов вам нужен?',
                type: 'checkbox',
                answers: [
                    'Гражданство РФ',
                    'ВНЖ',
                    'РВП',
                    'Патент'
                ]
            },
            {
                question : 'Есть ли у вас близкие родственники, являющиеся гражданами РФ?',
                type: 'radio',
                answers: [
                    'Да, муж/жена',
                    'Да, другие родственники',
                    'Да, родители',
                    'Нет'
                ]
            },
            {
                question : 'Обращались ли ранее за помощью в оформлении документов?',
                type: 'radio',
                answers: [
                    'Да, к вам',
                    'Нет',
                    'Да, в другой центр'
                ]
            }
        ],
        quiz: false,
        data: [],
        step: 0,
        lastStep: false,
        prevNextLastStep: false
    },
    getters: {
        statePrevNextLastStep(state) {
            return state.prevNextLastStep
        },
        stateQuiz(state) {
            return state.quiz
        },
        getQuestions(state){
            return state.questionAnswer
        },
        getStep(state){
            return state.step
        },
        getLastStep(state){
            return state.lastStep
        },
        getDataAnswer(state){
            return state.data
        }
    },
    mutations: {
        prevNextLastStepTrue(state) {
            state.prevNextLastStep = true
        },
        prevNextLastStepFalse(state) {
            state.prevNextLastStep = false
        },
        quizTrue(state) {
          state.quiz = true
        },
        changeStep(state,payload){
            state.step += payload
        },
        set(state,payload){
            state.data[state.step] = payload
            // console.log(state.data)
        },
        closeLastStep(state){
            state.lastStep = false
        },
        clearData(state){
            state.data = []
            state.step = 0
        }
    },
    actions: {
        prevNextLastStepTrue ({state, commit}) {
            commit('prevNextLastStepTrue')
        },
        prevNextLastStepFalse ({state, commit}) {
            commit('prevNextLastStepFalse')
        },
        prevNext({state, commit}, value) {
            console.log(state.data)
            if (value <= 0) {
                commit('changeStep', value)
            } else {
                if (!state.data[state.step] || state.data[state.step].answer === '') {
                    alert('Выберите вариант ответа!')
                    return false
                }
                if (state.step === state.questionAnswer.length - 1) {
                    state.lastStep = true
                    state.quiz = false
                    return false
                }
                commit('changeStep', value)
            }

        },
        setData({commit,state}, value) {
            commit('set', value)
        },
        closeLastStep({commit}){
            commit('closeLastStep')
        },
        clearData({commit}){
            commit('clearData')
        }
    }

};
